import { getClientHints } from '../clientHints';
import { expect } from 'chai';

describe('clientHints', () => {
  it('checks consistency', () => {
    // Get hints from userAgent string
    const hintsFromString = getClientHints(navigator.userAgent);
    // Get default hints (depending upon browser support,
    // may be computed from NavigatorUAData)
    const hintsFromDefault = getClientHints();

    // check that clientHints attributes, as determined by either NavigatorUAData processing
    // or userAgent string parsing, are consistent
    expect(hintsFromString.browser).to.equal(hintsFromDefault.browser);
    expect(hintsFromString.browserMajorVersion).to.equal(hintsFromDefault.browserMajorVersion);
    expect(hintsFromString.deviceType).to.equal(hintsFromDefault.deviceType);
    expect(hintsFromString.platform).to.equal(hintsFromDefault.platform);
  });
});
